.wrapper {
    display: flex;
    flex-direction: row;
    padding-top: 1em;
  }
  
  .profile-information{
    display: flex;
    flex-direction: column;
    max-width: 30%;
    min-width: 30%;
    background-color: lightgray;
  }
  
  .thumbnail-container {
    height: 300px;
    max-height: 300px;
    min-height: 300px;
  }
  
  .thumbnail-image {
    max-height: 100%;
    max-width: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  
  .series-information{
    display: flex;
    flex-direction: column;
    padding-left: 1em;
    max-width: 70%;
    min-width: 70%;
  }

